<template>
    <div class="post-hot-comment-com">
        <div class="avatar">
            <img class="avatar-img" src="" alt="">
        </div>
        <div class="comment-container">
            <div class="comment-top">
                <p class="comment-top-name">快乐的小绵羊</p>
                <div class="comment-top-hold">
                    <span class="icon-hold"></span>
                    <span class="comment-top-num">10</span>
                </div>
            </div>
            <div class="comment-detail">
                <p>或者是移民的做法，其背后隐藏诸多风险，法律尚不存在。或者是移民的做法，其背后隐藏诸多风险，法律尚不存在。</p>
            </div>
            <div class="comment-reply">
                <span>查看<span class="color-r">18000</span>条回复&nbsp;>></span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "hot-comment",
        props: ["comment-detail"]
    }
</script>

<style lang="scss">
    @import '../../../common/css/utils/mixins';
    %border-test {
        border: 1px solid red;
    }
    .post-hot-comment-com {
        position: relative;
        border-top: 1px solid #EEE;
        padding: .24rem .3rem;

        .avatar {
            display: inline-block;
            position: absolute;
        }

        .comment-container {
            display: inline-block;
            max-width: 5.8rem;
            width: 5.8rem;
            margin-left: 1.11rem;
            padding-top: .14rem;
        }
        .comment-top {
            position: relative;
            padding-bottom: .05rem;
            &-name {
                display: inline-block;
                max-width: 3.5rem;
                @include ellipsis;
                font-size: .28rem;
                color: #1a5da1;
            }
            &-hold {
                position: absolute;
                right: 0;
                top: 0;
                line-height: .24rem;
            }
            &-num {
                margin-top: .1rem;
                font-size: .24rem;
                color: #979797;
            }
        }
        .comment-detail {
            padding-right: .3rem;
            margin-top: .18rem;
            & > p {
                @include ellipsisMulti(3);
                font-size: .32rem;
                color: #1a1a1a;
            }
        }
        .comment-reply {
            margin-top: .24rem;
            & > span {
                font-size: .28rem;
                color: #bbbbbb;
            }
            .color-r {
                color: #eb4332;
            }
        }
    }
</style>



