<template>
    <div class="rec-chunk">
        <div class="post-user">
            <div class="avatar">
                <img class="avatar-img" src="" alt="">
                <span class="avatar-icon icon-woman"></span>
            </div>
            <div class="user">
                <span class="user-name">测试用户名</span>
                <div class="user-detail">
                    <span class="icon-position"></span>
                    <span class="chunk-place">深圳</span>
                    <span>15:31</span>
                </div>
            </div>
        </div>
        <div class="chunk-img-con">
            <img src="" alt="">
        </div>
        <div class="chunk-detail">
            <p>曾经我们也是好朋友，从哪以后就没再有联系了，不过无论过多久，那段记忆都没有忘</p>
        </div>
    </div>
</template>

<script>
    export default {}
</script>

<style lang="scss">
    @import '../../../common/css/utils/mixins';
    .rec-chunk {
        display: inline-block;
        width: 3.28rem;
        height: 5.2rem;
        background-color: #FFF;
        margin: .32rem 0 0 .32rem;
        .post-user {
            .user {
                margin-left: 0;
                padding-left: .15rem;
                .user-name {
                    max-width: 1.7rem;
                    line-height: 1.5;
                    font-size: .24rem;
                }
            }
            .chunk-place {
                display: inline-block;
                max-width: .85rem;
                @include ellipsis
            }
        }
        .chunk-img-con {
            width: 2.7rem;
            height: 2.44rem;
            margin: 0 auto;
            & > img {
                width: 100%;
                height: 100%;
            }
        }
        .chunk-detail {
            width: 2.8rem;
            margin: .24rem auto;
            & > p {
                @include ellipsisMulti(3);
                font-size: .24rem;
                line-height: 1.5;
                color: #1a1a1a;
            }

        }
    }
</style>



