<template>
    <div class="post-share">
        <!--顶部发帖人-->
        <div class="post-user">
            <div class="avatar">
                <img class="avatar-img" src="" alt="">
                <span class="avatar-icon icon-man"></span>
            </div>
            <div class="user">
                <span class="user-name">测试用户名</span>
                <div class="user-detail">
                    <span class="icon-position"></span>
                    <span class="user-place">深圳</span>
                    <span>15:31</span>
                </div>
            </div>
        </div>
        <!--帖子详情-->
        <div class="post-detail">
            <div class="content"></div>
            <div class="content-img">
                <!--<img src="1.jpg" alt="">-->
            </div>
            <div class="comment-summary">
                <div class="summary-avatar-container" v-if="summaryAvatars.length">
                    <template v-for="(item, index) in summaryAvatars">
                        <img class="summary-avatar" :class="{'avatar-move': index != 0}" :src="item.avatarSrc" alt="">
                    </template>
                    <span class="icon-ellipsis" v-if="summaryAvatars.length > 2"></span>
                </div>
                <div class="summary-num">
                    <span>245578人评论过</span>
                </div>
                <div class="summary-love">
                    <span class="icon-love"></span>
                    <span class="summary-font">128</span>
                </div>
                <div class="summary-chat">
                    <span class="icon-chat"></span>
                    <span class="summary-font">1280</span>
                </div>
            </div>
        </div>
        <!--热门评论-->
        <div class="post-hot-comment">
            <div class="module-title">
                <span>热门评论</span> <span>(20)</span>
            </div>
            <hot-comment></hot-comment>
            <div class="comment-split"></div>
            <div class="more more-hot-comment">
                <span>查看更多评论</span>
            </div>
        </div>
        <!--内容推荐-->
        <div class="post-recommendation">
            <div class="module-title">
                <span>精彩内容</span>
            </div>
            <div class="chunk-container">
                <template v-for="item in 4">
                    <recommendation-chunk :recommendation="item"></recommendation-chunk>
                </template>
            </div>
        </div>
        <div class="more go-more">
            <span>我想看更多</span>
        </div>
    </div>
</template>

<script>
    import HotComment from "./HotComment";
    import RecommendationChunk from "./RecommendationChunk";
    export default {
        data: function () {
            return {
                summaryAvatars: [1,2,3]
            }
        },
        components: {
            "hot-comment": HotComment,
            "recommendation-chunk": RecommendationChunk
        }

    }
</script>

<style lang="scss">
    @import "../../../common/css/utils/mixins";

    .post-share {
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        background-color: #EEE;

        .post-user, .post-detail, .post-hot-comment {
            background-color: #FFF;
        }

        .post-detail, .post-hot-comment {
            background-color: #FFF;
        }

        /*顶部发帖人信息*/
        .post-user {
            padding: .19rem 0 .19rem .3rem;
            line-height: 1;
            height: 1.12rem;
            .avatar {
                bottom: .21rem;
            }
            .user {
                /*长度限制*/
                display: inline-block;
                margin-left: .32rem;
                &-name {
                    display: inline-block;
                    height: .28rem;
                    max-width: 4rem;
                    @include ellipsis;
                    font-size: .28rem;
                    font-weight: 500;
                    line-height: 1;
                    color: #1a1a1a;
                }
                &-detail {
                    margin-top: .16rem;
                    font-size: .22rem;
                    line-height: 1;
                    color: #BBB;
                    .user-place {
                        display: inline-block;
                        max-width: 3rem;
                        @include ellipsis;
                    }
                }
            }
        }

        /*头像 带性别显示*/
        .avatar {
            position: relative;
            display: inline-block;
            width: .72rem;
            &-img {
                display: inline-block;
                width: .72rem;
                height: .72rem;
                border-radius: 50%;
            }
            &-icon {
                position: absolute;
                top: .44rem;
                right: 0;
            }
        }

        /*帖子详情*/
        .post-detail {
            margin: .01rem 0 .24rem;
            .comment-summary {
                position: relative;
                height: 1rem;
                width: 100%;
                padding: .22rem .32rem;
                line-height: 1.4;
            }
            .summary-avatar-container {
                display: inline-block;
                .icon-ellipsis {
                    margin-left: -.35rem
                }
            }
            .summary-avatar {
                display: inline-block;
                width: .54rem;
                height: .54rem;
                border: none;
                border-radius: 50%;
                background-color: #DDD;
                &.avatar-move {
                    margin-left: -0.25rem;
                }
            }

            .summary-num {
                display: inline-block;
                margin-left: .05rem;
                font-size: .24rem;
                color: #1a1a1a;
            }

            .summary-love {
                display: inline-block;
                position: absolute;
                right: 1.75rem;
            }
            .summary-chat {
                display: inline-block;
                position: absolute;
                right: .32rem;
            }
            .summary-font {
                font-size: .24rem;
                color: #808080;
            }
        }

        .module-title {
            height: .88rem;
            padding: 0 .32rem;
            line-height: .88rem;
            font-size: .28rem;
            font-weight: 500;
            color: #1a1a1a;
            & > span {
                margin-right: .1rem;
            }
        }
        /*热门评论*/
        .post-hot-comment {
            padding-bottom: .32rem;
            .comment-split {
                width: 6.84rem;
                margin: .32rem auto;
                margin-top: 0;
                outline: none;
                border-top: 1px solid #EEE;
            }
            .more-hot-comment {
                width: 6.86rem;
                margin: 0 auto;
                border-radius: .04rem;
                background-color: #e0f0ff;
                border: solid .01rem #348feb;
                & > span {
                    color: #378ce3;
                }
            }
        }
        /*精彩推荐*/
        .post-recommendation {
            margin-top: .32rem;
            padding-bottom: .94rem;
            .module-title {
                background-color: #FFF;
            }
            .chunk-container {

            }
        }
        /*看更多*/
        .go-more {
            position: fixed;
            bottom: 0;
            left: 0;
            width: 100%;
            background-color: #046fdb;
            & > span {
                color: #FFF;
            }
        }

        .more {
            height: .96rem;
            text-align: center;
            line-height: .85rem;
            & > span {
                letter-spacing: 2px;
                font-size: .32rem;
            }
        }
    }
</style>



